/*# sourceMappingURL=MobileNav.css.map */
body,
html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

@media only screen and (min-width: 375px) {
  .hpBodyDiv {
    position: relative;
    width: 95%;
    margin: auto;
  }
  .hpHeaderDiv {
    background: linear-gradient(to bottom, rgba(255, 255, 255, 0.1), black), url(/static/media/Untitled.f38bce2d.jpg);
    background-position: center;
    background-size: cover;
    height: 600px;
    background-repeat: no-repeat;
    font-family: "Playfair Display", sans-serif;
    border-radius: 10px 10px 10px 10px;
    margin-bottom: 10px;
  }
  .hpHeaderDiv h1 {
    color: #ffffff;
    text-align: center;
    font-size: 40px;
    padding-top: 360px;
  }
  .hpHeaderDiv h3 {
    text-align: center;
    font-size: 20px;
    color: #ffffff;
  }
  .hpBody div {
    display: -ms-grid;
    display: grid;
    grid-row-gap: 10px;
    -ms-grid-rows: 200px 200px 200px;
        grid-template-rows: 200px 200px 200px;
  }
  .hpBody .podcastDiv {
    background: linear-gradient(to bottom, rgba(255, 255, 255, 0.2), black), url(/static/media/radio.df51c623.jpg);
    background-position: top;
    background-size: cover;
    border-radius: 10px;
  }
  .hpBody .podcastDiv h1 {
    color: #ffffff;
    font-family: "Playfair Display", sans-serif;
    text-align: center;
    margin-top: 150px;
  }
  .hpBody .partyDiv {
    background: linear-gradient(to bottom, rgba(255, 255, 255, 0.2), black), url(/static/media/Event.fb568870.jpg);
    background-position: top;
    background-size: cover;
    border-radius: 10px;
  }
  .hpBody .partyDiv h1 {
    color: #ffffff;
    font-family: "Playfair Display", sans-serif;
    text-align: center;
    margin-top: 150px;
  }
  .hpBody .blogDiv {
    background: linear-gradient(to bottom, rgba(255, 255, 255, 0.2), black), url(/static/media/blog.382084c3.jpg);
    background-position: top;
    background-size: cover;
    border-radius: 10px;
  }
  .hpBody .blogDiv h1 {
    color: #ffffff;
    font-family: "Playfair Display", sans-serif;
    text-align: center;
    margin-top: 150px;
  }
  .ph {
    height: 600px;
  }
}
